import { createRouter, createWebHistory } from "vue-router"
import { session } from "./data/session"
import { userResource } from "@/data/user"

const routes = [
    {
        name: "Login",
        path: "/account/login",
        component: () => import("@/pages/Login.vue"),
    },
    // Customer Routes
    {
        path: '/',
        component: () => import("@/pages/CustomerView.vue"),
        children: [
            { path: '', name: 'Home', component: () => import("@/pages/CustomerView.vue") },
            { path: 'cart', name: 'Cart', component: () => import("@/pages/CustomerView.vue") },
            { path: 'orders', name: 'Orders', component: () => import("@/pages/CustomerView.vue") },
            { path: ':pincode', name: 'ShopPincode', component: () => import("@/pages/CustomerView.vue") },
            { path: ':pincode/:society', name: 'ShopSociety', component: () => import("@/pages/CustomerView.vue") },
            { path: ':pincode/:society/:category', name: 'ShopCategory', component: () => import("@/pages/CustomerView.vue") },
            { path: ':pincode/:society/:category/:sellerName', name: 'SellerItems', component: () => import("@/pages/CustomerView.vue"), props: true }
        ]
    },
    // Seller Routes
    {
        path: '/seller',
        component: () => import("@/pages/SellerView.vue"),
        children: [
            { path: '', name: 'SellerDashboard', component: () => import("@/pages/SellerView.vue") },
            { path: 'orders', name: 'SellerOrders', component: () => import("@/pages/SellerView.vue") },
            { path: 'products', name: 'SellerProducts', component: () => import("@/pages/SellerView.vue") }
        ]
    }
]

const router = createRouter({
    history: createWebHistory("/frontend"),
    routes,
})

router.beforeEach(async (to, from, next) => {
    let isLoggedIn = session.isLoggedIn
    try { await userResource.promise } catch (error) { isLoggedIn = false }

    // Session Restore Logic (Refresh hone par)
    if (isLoggedIn && !session.role) {
        try {
            const res = await fetch('/api/method/doppio_demo.api.get_user_role_info')
            const data = await res.json()
            if (data.message) {
                session.role = data.message.role
                session.sellerCompany = data.message.company
            }
        } catch (e) { console.log(e) }
    }

    if (to.name !== "Login" && !isLoggedIn) {
        next({ name: "Login" })
    } 
    else if (to.name === "Login" && isLoggedIn) {
        // Agar logged in banda Login page pe aaye
        next(session.role === 'Seller' ? { name: 'SellerDashboard' } : { name: 'Home' })
    }
    else if (to.path.startsWith('/seller') && session.role !== 'Seller') {
        // Security: Customer Seller page nahi khol sakta
        next({ name: 'Home' })
    } 
    else {
        next()
    }
})

export default router